.footer {
  background-color: #f9f9f9;
  width: 100%;
  height: 8rem;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  color: #616161;
  font-size: 1rem;
  .iconsWrapper {
    margin-top: 1.5rem;
    svg {
      color: #616161;
    }
    width: 175px;
    display: flex;
    justify-content: space-around;
    flex-direction: row;
    align-items: center;
  }
}

@media only screen and (max-width: 767px) {
  .footer {
    height: 8rem;
    font-size: 0.9rem;
  }
}
